.crazy-things {
  padding: 0;
  margin: 0;
  background-color: #f2f2f2;
  border: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.crazy-things__label-for-users-crazy-things {
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 18px;
  line-height: 30px;
  color: #283645;
}

.crazy-things__container {
  margin: 0;
  padding: 0;
}

@media (min-width: 660px) {

  .crazy-things {
    width: 660px;
    min-height: 550px;
    background-color: #ffffff;
    border: 2px solid #e5e5e5;
  }

 .crazy-things__legend {
  margin-left: 60px;
  padding-left: 40px;
  padding-right: 40px;
  text-transform: uppercase;
 }

}

@media (min-width: 1200px) {

  .crazy-things {
    width: 940px;
    min-height: 228px;
  }

  .crazy-things__wrapper {
    display: flex;
    flex-wrap: wrap;
  }

  .crazy-things__legend {
   margin-left: 200px;
   padding-left: 40px;
   padding-right: 40px;
 }

}
